import React, { useRef, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Sticky, Container, Message, Button } from 'semantic-ui-react'
import { searchCountriesByName } from '../reducers/countriesReducer'
import { getCountryFeatures } from '../reducers/countryDataReducer'
import '../App1.css'
import loadable from '@loadable/component'

const Country = loadable(() => import('./CountryMobile'))
const MobileNav = loadable(() => import('../components/HeaderMobile'))
const CountriesTableMobile = loadable(() => import('../components/TableMobile'))

const NoMatches = () => {
  const dispatch = useDispatch()

  const clearInput = () => {
    dispatch(searchCountriesByName(''))
  }

  return (
    <Container>
      <Message compact info>
        <Message.Header>No matches, please try again.</Message.Header>
        <Button basic color="teal" onClick={clearInput}>
          OK
        </Button>
      </Message>
    </Container>
  )
}

const Countries = () => {
  const countriesFiltered = useSelector((state) => state.countries.filtered)
  const dispatch = useDispatch()

  useEffect(() => {
    if (countriesFiltered.length === 1) {
      const location = countriesFiltered[0].capitalInfo.latlng
      dispatch(getCountryFeatures(location))
    }
  }, [countriesFiltered])

  const contextRef = useRef()

  return (
    <div id="ref" ref={contextRef}>
      <Sticky id="Sticky" context={contextRef}>
        <MobileNav />
      </Sticky>
      {countriesFiltered.length === 1 ? (
        <Country data={countriesFiltered[0]} />
      ) : countriesFiltered.length === 0 ? (
        <NoMatches />
      ) : (
        <CountriesTableMobile />
      )}
    </div>
  )
}

export default Countries
